        .row.justify-content.center.mb-3
          .col-lg-6.mb-4
            p.mb-3 Como concepto de comunicacin escrita se puede decir que es una forma de comunicacin que permite expresar sentimientos, emociones, conocimientos, opiniones, etc. por medio del papel, desde los tiempos antiguos y en la actualidad a travs de los medios digitales, en los que se utiliza como recurso dicho tipo de lenguaje. 
            p.mb-3 De igual forma, la comunicacin escrita se caracteriza por tratarse de una manera de comunicacin que no est supeditada a la inmediatez y tampoco a un espacio fsico para poderse dar, al contrario, el emisor responsable de emitir el mensaje puede construir un texto que contenga la informacin que desea transmitir y este puede llegar a un nmero indeterminado de receptores en diferentes lugares y momentos.
            .cajon.color-acento-contenido.p-4.mb-3
              p.mb-0 Otra caracterstica de la comunicacin escrita es que perdura en el tiempo, ya que los escritos permanecen y no se olvidan como suele suceder con la comunicacin oral, adems, esta forma de comunicacin logra abarcar un mayor nmero de receptores, por lo cual, se puede calificar como un sistema de comunicacin masivo. 
            p.mb-0 Adicionalmente, es importante destacar que, aunque se trata de una forma de comunicacin, esta no es precisamente de doble va, ya que la relacin que se establece entre el emisor y el receptor es simplemente la de envo del mensaje y recepcin del mismo, ya que en la mayora de los casos el emisor no obtiene del receptor una respuesta inmediata, es ms, a veces ni se recibe.
